import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';

import Error from '../error';
import Message from '../message';

@observer
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isCapsLock: false,
            email: this.props.email,
            passwordType: 'password',
            emailIsAvailable: false,
        };
    }

    handleEmailInput = async (e) => {
        const { userStore } = this.props.stores;
        const email = e.target.value;
        this.setState({ email });
        clearTimeout(this.checkEmailTimer);
        this.checkEmailTimer = setTimeout(() => userStore.checkEmail(email), 1000);
    }

    register = async () => {
        const { email, password } = this.state;
        const { userStore, appState } = this.props.stores;
        const { language = 'en' } = appState;
        const { skipPubSub } = this.props;
        const { fingerprint, jwtToken } = appState;
        await userStore.registerUser({ email, password, fingerprint, jwtToken, skipPubSub, language });
        await userStore.getInfo();
    };

    removeError = () => {
        this.setState({ error: '', errorIcon: '' });
    }

    removeMessage = () => {
        this.setState({ message: '', messageIcon: '' });
    }

    togglePassword = () => {
        let { passwordType } = this.state;
        if (passwordType === 'password') {
            passwordType = 'text';
        } else {
            passwordType = 'password';
        }
        this.setState({ passwordType });
    }

    updateCapsLockStatus = (isCapsLock) => {
        this.setState({
            isCapsLock,
        });
    }

    login = () => {
        const { email = '' } = this.state;
        const { routeLogin = `/${email}` } = this.props;

        route(routeLogin);
    }

    forgotten = () => {
        const { email = '' } = this.state;
        const { routeLoginForgot = `/forgotten/${email}` } = this.props;
        route(routeLoginForgot);
    }

    changeLanguage = (e) => {
        const lang = e.target.value;
		const { appState } = this.props.stores;
        appState.setLanguage(lang);
    }

    componentDidMount() {
        const { userStore } = this.props.stores;
        if (this.props.email) {
            clearTimeout(this.checkEmailTimer);
            this.checkEmailTimer = setTimeout(() => userStore.checkEmail(this.props.email), 1000);
        }
    }

    render() {
        const { isCapsLock, email, password, passwordType } = this.state;
        const { appState, userStore } = this.props.stores;
        const { showLogo = true } = this.props;
        const { language = 'en' } = appState;
        const { emailIsAvailable, emailMessage, login } = userStore;
        const { message, messageIcon, error, errorIcon } = login;

        const isValidEmail = util.validateEmail(email);
        const passwordScore = util.scorePassword(password);
        const passwordScoreNormalized = util.normalizeBetween(passwordScore, 0, 80, 0, 100, 100);
        const passwordStrength = util.checkPassStrength(password, ['bg-danger', 'bg-warning', 'bg-success', 'bg-success']);
        const isValidPassword = passwordScoreNormalized > 30;
        const isOkToRegister = isValidEmail && emailIsAvailable && isValidPassword;

        const darkmode = true;

        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                        {showLogo && <>
                            <div class='d-flex justify-content-center mb-5 mt-5'>
                                {darkmode ? <>
                                    <img src={`https://themusher.app/assets/logo-qpaws-dark.png`} height='100' />
                                </> : <>
                                    <img src={`https://themusher.app/assets/logo-qpaws-light.png`} height='100' />
                                </>}
                            </div>
                        </>}
                        {/* <div class='d-flex justify-content-center'>
                            <h5 class='mt-1 mb-5'><Text id='register.new-user'>Ny bruker</Text></h5>                        
                        </div> */}

                        <Error message={error} icon={errorIcon} remove={this.removeError} />
                        <Message message={message} icon={messageIcon} remove={this.removeMessage}  />

                        <div class='form-group'>
                            {/* <label for='emailInput'><i class='far fa-envelope' /> <Text id='register.email'>E-post</Text></label> */}
                            <Localizer>
                                <input type='email'
                                    class={`form-control form-control-lg ${email ? (isValidEmail && emailIsAvailable ? 'is-valid' : 'is-invalid') : ''}`}
                                    id='emailInput'
                                    onInput={this.handleEmailInput}
                                    value={email}
                                    onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                    placeholder={<Text id='register.email'>E-post</Text>}
                                />
                            </Localizer>
                            {email && isValidEmail && emailIsAvailable && <div class='valid-feedback' style={`display: block;`}>
                                {emailMessage}
                            </div>}
                            {email && !isValidEmail && <div class="invalid-feedback" style={'display: block;'}>
                                <Text id='register.email-not-valid'>Hmm, e-posten ser ikke helt riktig ut.</Text>
                            </div>}
                            {email && isValidEmail && !emailIsAvailable && <div class="invalid-feedback" style={'display: block;'}>
                                {emailMessage}
                            </div>}
                        </div>

                        <div class='form-group'>
                            {/* <label for='passwordInput'><i class='fas fa-key' /> <Text id='register.password'>Passord</Text></label> */}
                            <div class='input-group'>
                                <Localizer>
                                    <input type={passwordType}
                                        class={`form-control form-control-lg ${password ? (isValidPassword ? 'is-valid' : 'is-invalid') : ''}`}
                                        id='passwordInput'
                                        onInput={linkState(this, 'password')}
                                        value={password}
                                        onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                        placeholder={<Text id='register.password'>Passord</Text>}
                                    />
                                </Localizer>
                                <div class='input-group-append'>
                                    <button class='btn btn-primary' type='button'
                                        onClick={this.togglePassword}
                                    >
                                        <i class={`fas fa-eye${passwordType === 'text' ? '-slash' : ''}`} />
                                    </button>
                                </div>
                            </div>

                            <div class='progress' style='height: 2px;'>
                                <div class={`progress-bar ${passwordStrength}`} role='progressbar' style={`width: ${passwordScoreNormalized}%;`} />
                            </div>
                            <div class='invalid-feedback' style={`${passwordScoreNormalized > 0 && passwordScoreNormalized < 30 ? 'display: block' : ''}`}>
                                <Text id='register.password-too-weak'>Passordet ditt er for svakt. Fors칮k et annet passord eller legg til flere bokstaver.</Text>
                            </div>
                            <div class='valid-feedback text-warning' style={`${passwordScoreNormalized >= 30 && passwordScoreNormalized < 70 ? 'display: block' : ''}`}>
                                <Text id='register.password-weak'>Passordet ditt er svakt, men vis skal gj칮re v친rt beste for 친 passe p친 deg.</Text>
                            </div>
                            <div class='valid-feedback' style={`${passwordScoreNormalized >= 70 ? 'display: block' : ''}`}>
                                <Text id='register.password-ok'>Passordet ditt ser bra ut.</Text>
                            </div>
                        </div>

                        {isCapsLock && <div class='alert alert-danger' role='alert'>
                            <Text id='register.caps-lock-on'>CAPS LOCK er p친!</Text>
                        </div>}

                        <button class={`btn btn-lg btn-block ${isOkToRegister ? 'btn-primary' : 'btn-primary'}`} type='button'
                            onClick={this.register}
                            disabled={!isOkToRegister}
                        >
                            <i class='far fa-save mr-2' />
                            <Text id='register.register'>Registrer meg</Text>
                        </button>

                        <small class='form-text text-muted mt-5 text-center'>
                            <button class='btn btn-small btn-link text-white'
                                onClick={this.login}
                            >
                               <Text id='register.i-have-an-account'>Jeg har visst konto likevel. G친 til login siden.</Text>
                            </button>
                        </small>

                    </div>
                </div>
            </div>
        );
    }
}

export default Login;
